<template>
  <el-container>
     <el-header>
      <header-buttons :showRefresh="true"  @buttonClickEvent="buttonClickEvent" cssType="1"></header-buttons>
    </el-header>
    <el-main class="view">
      <el-row>
         <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" class="tool-area right">
           <div class="tool-content">
            <el-row>
              <el-col :span="24">
                <VXETable
                    :tableName="'test'"
                    :height="$options.filters.getTableHeight(200)"
                    :tableLoading="tableLoading"
                    :pager-config="toolListTable.page"
                    :tableDatas="toolListTable.data"
                    :tableColumns="myTableColumns"
                    :tablePage="toolListTable.page"
                    :highlightCurrentRow="true"
                    @onTablePageChange="handleToolListTablePageChange"
                    ref="toolListGrid"
                  >
                </VXETable>
              </el-col>
            </el-row>
           </div>
         </el-col>
      </el-row>
    </el-main>

    <el-dialog
      :class="['relaDevice',$store.state.dialogMarginClass]"
      width="80%"
      :title="'关联设备'"
      :visible.sync="dialogVisibleRelated"
    >
      <deviceDialog
        ref="deviceDialog"
        v-if="dialogVisibleRelated"
        :isFunc="true"
        :checkedData="checkedData"
        :currentChangeRow="currentChangeRow"
        @hiddenRelated="hiddenRelated"
        @saveRelatedDevice="saveRelatedDevice"
      ></deviceDialog>
    </el-dialog>
  </el-container>
</template>

<script>
import js from '@/assets/js/energy/releMac/index.js';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/energy/releMac/index.less';
</style>
