// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入组件
import deviceDialog from '@/components/common/DeviceDialog/deviceDialogx';
// 引入警告信息
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';

import { EQUIPMENT_DATA, BTN_TEXT, MSG, VARIABLE, STATUS_CODE, TOOL_STATUS } from '@/utils/constantsFile';

import vueQr from 'vue-qr';
import {
  deviceElectricityMeterRelationshipGetPageElectricityMeterRelationship,
  deviceElectricityMeterRelationshipUpdateElectricityMeterRelationshipDevice
} from '@/api/releMac.js';
export default {
  name: 'ToolNotify',
  computed: {
    // 表格使用的表头配置
    myTableColumns: function() {
      if ((Array.isArray(this.savedCustomColumns)) && this.savedCustomColumns.length > 0) {
        return this.savedCustomColumns;
      } else {
        return this.toolListTable.columns;
      }
    }
  },
  components: {
    vueQr,
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    deviceDialog
  },
  data() {
    const setValidateForm = ({ column, row, cellValue }) => {
      return new Promise((resolve, reject) => {
        return this.$options.filters.formatterTableValidate(column, cellValue, this.extendPropertyValueVoList, resolve, reject);
      });
    };
    const checCheckboxkMethod = ({ row }) => {
      return parseInt(row.toolState) !== 2;
    };
    return {
      setValidateForm: setValidateForm,
      checCheckboxkMethod: checCheckboxkMethod,
      VARIABLE: VARIABLE,
      TOOL_STATUS: TOOL_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      title: EQUIPMENT_DATA.TOOL_NOTIFY,
      toolListTable: {
        checkboxConfig: {
          checkMethod: checCheckboxkMethod
        },
        page: {
          currentPage: 1,
          pageSize: 10,
          total: 30,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            field: 'meterName',
            title: '电表名称',
            minWidth: '160',
            visible: true,
            align: 'center'
          },
          {
            field: 'deviceNameList',
            title: '已关联设备',
            minWidth: '160',
            visible: true,
            align: 'center'
          },
          {
            field: '操作',
            title: BTN_TEXT.OPERATION,
            width: '100',
            fixed: 'right',
            align: 'center',
            slots: {
              default: ({ row, column }) => {
                return [
                  <el-button onClick={() => this.showRelated(row, column)} type="text">关联设备</el-button>
                ];
              }
            }
          }
        ],
        data: [],
        isSearchBoxShow: false,
        searchList: [],
        searchData: [],
        orderByData: {}
      }, // 所属工具列表
      tableLoading: false,
      // 显示关联模态框
      dialogVisibleRelated: false,
      currentChangeRow: {},
      checkedData: []
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      this[`${type}`]();
    },
    refresh() {
      this.getToolListData();
    },
    /**
     * @description: 获取工具列表数据
     * @param {string} code 工具代码
     * @return: void
     */
    getToolListData() {
      this.tableLoading = true;
      var params = {
        current: this.toolListTable.page.currentPage,
        size: this.toolListTable.page.pageSize
      };
      deviceElectricityMeterRelationshipGetPageElectricityMeterRelationship(params).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.toolListTable.data = res.rows;
          this.toolListTable.page.total = res.total;
          this.tableLoading = false;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 执行table分页
     * @param {string} currentPage 第几页
     * @param {string} pageSize 一页几条
     * @return: void
     */
    handleToolListTablePageChange(currentPage, pageSize) {
      this.toolListTable.page.currentPage = currentPage;
      this.toolListTable.page.pageSize = pageSize;
      this.getToolListData(this.selectCode);
    },
    /**
     * @description: 关闭关联模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    showRelated(row) {
      this.currentChangeRow = row;
      this.checkedData = [];
      if (row.deviceCodeList && row.deviceCodeList.length > 0) {
        this.checkedData = JSON.parse(JSON.stringify(row.deviceCodeList));
      }
      this.dialogVisibleRelated = true;
      // this.$refs.relatedDevice.clearCheckboxRow();
      // this.$refs.relatedDevice.deviceChecks = [];
    },
    /**
     * @description: 关闭关联模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    hiddenRelated() {
      this.dialogVisibleRelated = false;
      // this.$refs.relatedDevice.clearCheckboxRow();
      // this.$refs.relatedDevice.deviceChecks = [];
    },
    /**
     * @description: 保存用户关联设备
     * @param checkedData //勾选的数据
     * @param tableDatas //当前table数据
     * @param contrastData //原始关联的数据
     * @return: 无
     * @author: ZJM
     */
    async saveRelatedDevice(checkedData, tableDatas) {
      console.log(checkedData, tableDatas, this.checkedData);
      try {
        var deviceCodeList = [];
        // var row = {};
        // row = this.$refs.mmmTable.getRadioRecord();

        // 先把已选中的存入绑定列表
        for (var i = 0; i < checkedData.length; i++) {
          deviceCodeList.push(checkedData[i].deviceCode);
        }

        // 若已绑定过数据则进行遍历比较
        if (this.checkedData.length > 0) {
          // 遍历绑定过的数据
          for (var j = 0; j < this.checkedData.length; j++) {
            // 设定标记数，用于判断已绑定的数据是否存在再当前列表中
            var flag = 0;
            // 遍历当前数据列表
            for (var x = 0; x < tableDatas.length; x++) {
              // 若当前列表的设备和已绑定过的设备相同
              if (this.checkedData[j] === tableDatas[x].deviceCode) {
                // 标记是否存在相同设备
                flag += 1;
                // 判断该设备是否在绑定列表中已存在，若不存在则加入
                console.log(deviceCodeList, tableDatas[x].deviceCode);
                if (deviceCodeList.indexOf(tableDatas[x].deviceCode) === -1) {
                  deviceCodeList.push(tableDatas[x].deviceCode);
                }
              }
            }
            // 若已帮的设备未存在当前列表
            if (flag === 0) {
              // 该设备是否存在在绑定列表中，若不存在则加入
              if (deviceCodeList.indexOf(this.checkedData[j]) === -1) {
                deviceCodeList.push(this.checkedData[j]);
              }
            }
          }
        }
        console.log(deviceCodeList);
        const res = await deviceElectricityMeterRelationshipUpdateElectricityMeterRelationshipDevice(
          {
            deviceCodeList: deviceCodeList,
            meterCode: this.currentChangeRow.meterCode
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.getToolListData();
          var arr = [];
          for (var w = 0; w < res.rows.length; w++) {
            arr.push(res.rows[w].deviceCode);
          }
          this.checkedData = arr;
          // this.getWorkshopTypeRelationshipTemplateData();
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    }
  },
  created() {
    this.getToolListData();
  }
};
